.voluncard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2em;
  background-color: rgba(255, 255, 255, 0.2); /* Semi-transparent white */
  border-radius: 10px;
  margin-bottom: 1em;
  height: fit-content;
  width: 45rem;
  backdrop-filter: blur(10px); /* Creates the glass effect */
  -webkit-backdrop-filter: blur(10px); /* For Safari */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: subtle shadow */
  border: 1px solid rgba(255, 255, 255, 0.3); /* Optional: light border */
}

.volun {
  height: 40vh;
  overflow-y: scroll;
  margin-bottom: 2rem;
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;
}

.voluncard__content {
    flex: 1;
}

.voluncard__title {
    font-size: 1rem;
    margin-bottom: 0.1em;
    color: white;
}

.voluncard__description {
    font-size: 0.8em;
    color: #fff; 
    margin-top: -0.8rem;
}

.voluncard__carousel {
    width: 220px; 
    margin-left: 2em;
    margin-right: 2rem;
}

.voluncard__carousel-image {
    width: 100%;
    height: 13rem;
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.3); /* Optional: image border */
}

.voluncard__logos {
    display: flex;
    margin-bottom: 1em;
    margin-top: -1rem;
  }
  
  .voluncard__logo {
    width: 100px; /* Adjust the size of the logos */
    height: auto;
    margin-right: 1em;
    margin-bottom: -1rem;
  }
  
  .voluncard__logo-image {
    width: 100%;
    height: 100%;
    object-fit: contain; /* Maintain aspect ratio */
  }
  
  .voluncard__title {
    font-size: 1.5em;
    margin-bottom: 0.5em;
  }